import { customAttribute } from '../decorator/exported';
import { IElement, IAttribute, IListener } from '../interface/exported';
import { ViewController, ViewParser } from '../view/exported';
import { BindingEngine } from '../binding/exported';



/**
 * removes dom when expression is undefined/null''0
 *
 */
@customAttribute('if.bind')
export class IfAttribute implements IAttribute {
    public $element: HTMLElement;
    public $attribute: Attr;
    public $parent: IElement;
    private value: string | null | undefined;
    private elementClone: Node;
    private anchor: Node;
    private added: boolean;
    private subscribeCall: IListener;
    private $view: any;


    /**
     * created
     *
     */
    public created() {

        // get current value;
        this.value = this.$attribute.value;

        // remove the if.bind attribute
        this.$element.attributes.removeNamedItem('if.bind');
        if (this.$element && (this.$element as any).$view) {
            this.$view = (this.$element as any).$view;
        }

        // so we know our template in repeat isnt the correct one
        (this.$element as any).$ifbindview = true;

        // clone node, we need this clone later when we add it
        this.elementClone = this.$element.cloneNode(true);

        // hide it for now, we will remove it when we are attached to parent
        this.$element.style.display = 'None';

        // set variable for later
        this.added = false;


    }



    /**
     * attached
     *
     */
    public attached() {

        // create anchor so where know where to inject it
        // this needs to be after we attched it since we need parentNode to do this
        this.anchor = document.createComment('mf-if-bind');
        if (this.$view) {
            (this.anchor as any).$view = this.$view;
        }

        // add anchor
        this.$element.parentNode.insertBefore(this.anchor, this.$element);

        // remove main element
        this.remove();

        this.subscribeCall = {
            name: 'ifAttribute',
            value: this.value,
            call: (newValue: any) => {

                switch (true) {
                    case newValue && !this.added:
                        this.added = true;
                        this.add();
                        break;
                    case !newValue && this.added:
                        this.remove();
                        break;
                }
            }
        };


        BindingEngine.subscribeClassProperty(this.$parent, this.value, this.subscribeCall);
    }



    /**
     * detached
     *
     */
    public detached(): void {
        BindingEngine.unSubscribeClassProperty(this.$parent, this.subscribeCall);
        this.remove();
        if (this.anchor) {
            this.anchor.parentNode.removeChild(this.anchor);
            this.anchor = null;
        }
    }



    /**
     * shows element/content
     *
     */
    public add() {
        // create clone
        const template = this.elementClone.cloneNode(true);

        // set clone as current element
        this.$element = (template as any);
        (template as any).$view = new ViewController(template);

        // get attach event for elements
        let controllers = ViewParser.parseNodes(this.$element, this.$parent, (template as any).$view);
        this.anchor.parentNode.insertBefore(this.$element, this.anchor);

        // if this even used after last rebuild?
        controllers.forEach((x) => {
            if (x.attached) {
                x.attached();
            }
        });
        controllers = null;


    }



    /**
     * removes content
     *
     */
    private remove() {
        if (this.added) {
            this.added = false;
            ViewParser.clearAllViews(this.$element);
        }
        if (this.$element) {
            this.anchor.parentNode.removeChild(this.$element);
            this.$element = null;
        }
    }



}
