// this is very simple atm
// need authStep, child router, on route not found fail back
import { EventAggregator } from './exported';
import { inject } from '../decorator/exported';
import { MfRouter } from '../element/exported';
import { IRouterExpression, IRouteConfigInternal, IRouteConfiguration, IRouteConfigurationInternal } from '../interface/exported';


@inject(EventAggregator)
export class Router {

    public isNavigating = true;

    private authCaller: { call: Function };
    private unknownCaller: { call: Function };
    private config: { [key: string]: IRouteConfigurationInternal } = {};
    private routes: string[] = [];
    private routesData: IRouteConfigInternal[] = [];
    private urlHash: string;
    private curRoute = 0;
    private lastRouter = '';
    private loadedRouterElements: MfRouter[] = [];
    private eventAggregator: EventAggregator;
    private allRoutes: string[] = [];
    private hashChangeBinded: any;
    private oldUrlHash: any;
    private skipNext: boolean;

    constructor(eventAggregator: EventAggregator) {
        this.eventAggregator = eventAggregator;
        this.urlHash = window.location.hash.replace('#', '');
        this.hashChangeBinded = this.hashChange.bind(this);
        this.init();
    }


    /**
     * helper class called by router element
     * need this for when element returns false on canNavigate
     * need to set back correct url
     *
     */
    public succeessRoute(type: any) {
        if (type.success) {
            this.oldUrlHash = this.urlHash;
        } else {
            this.urlHash = this.oldUrlHash;
            this.isNavigating = false;
            this.skipNext = true;
            window.location.hash = this.urlHash;

        }

    }


    /**
     * detached
     *
     */
    public detached() {
        window.removeEventListener('hashchange', this.hashChangeBinded);
    }

    /**
     * registers router elements in use
     *
     */
    public registerRouterElement(_class: MfRouter) {
        this.loadedRouterElements.push(_class);
    }



    /**
     * unregisters router elements in use
     *
     */
    public unregisterRouterElement(_class: MfRouter) {
        if (this.loadedRouterElements.indexOf(_class) !== -1) {
            this.loadedRouterElements.splice(this.loadedRouterElements.indexOf(_class), 1);
        }
    }



    /**
     * triggers when hash changes
     *
     */
    public hashChange() {
        if (!this.skipNext) {
            this.isNavigating = true;
            requestAnimationFrame(() => {
                this.parseRouters(0);
            });
        } else {
            this.skipNext = false;
        }
    }



    /**
     * called by routerview when they have attached
     *
     */
    public routerViewInit(caller: string) {
        this.isNavigating = true;
        requestAnimationFrame(() => {
            this.routerViewUpdate(caller);
        });

    }



    /**
     * add new router
     * Todo: remove in 1.0.0
     */
    public addRouter(config: IRouteConfiguration) {
        console.warn('use addRouterConfig, this will be removed in the future');
        this.parseAddedRouter((config as any));
    }

    /**
    * add new router config
    * Todo: remove in 1.0.0
    */
    public addRouterConfig(config: IRouteConfiguration) {
        this.parseAddedRouter((config as any));
    }



    /**
     * setAuthStep
     *
     */
    public setAuthHandler(authCaller: { call: Function }) {
        this.authCaller = authCaller;

    }



    /**
     * failedRoute
     *
     */
    public setUnknowRouteHandler(unknownCaller: { call: Function }) {
        this.unknownCaller = unknownCaller;

    }



    /**
     * called when it starts
     *
     */
    public getRoutesFor(name: string, includingExpressions?: boolean) {
        if (this.config[name]) {
            if (includingExpressions) {
                return this.config[name].routes;
            } else {
                return this.config[name].routes.filter((x: any) => x.isNav);
            }
        } else {
            return [];
        }
    }



    /**
     * returns current module path from router config
     *
     */
    public getCurrentRouteTitle(name: string): string {
        return this.config[name] ? this.config[name].routes[this.curRoute !== undefined ? this.curRoute : 0].title : undefined;
    }



    /**
     * returns current module path from router config
     *
     */
    public getCurrentRoute(name: string): string {
        return this.config[name] ? this.config[name].routes[this.curRoute !== undefined ? this.curRoute : 0].route : undefined;
    }


    /**
     * returns current module path from router config
     *
     */
    public getCurrentRouteIndex(name: string): number {
        return this.config[name] ? this.curRoute : 0;
    }



    /**
     * returns current module path from router config
     *
     */
    public getCurrentModule(name: string): string {
        return this.config[name] ? this.config[name].routes[this.curRoute !== undefined ? this.curRoute : 0].modulePath : undefined;
    }



    /**
     * returns current module class(name) from router config
     *
     */
    public getCurrentClass(name: string): string {
        return this.config[name] ? this.config[name].routes[this.curRoute !== undefined ? this.curRoute : 0].moduleClass : undefined;
    }



    /**
     * returns the content of router config load function (promise)
     *
     */
    public async loadCurrentModule(name: string): Promise<string> {
        if (this.lastRouter !== name) {
            this.curRoute = 0;
        }

        // this need to be async..
        return await this.config[name] ? this.config[name].routes[this.curRoute !== undefined ? this.curRoute : 0].load() : undefined;
    }



    /**
     * gets params
     *
     */
    public getParams(expression: any, hash: string) {
        const urls = hash.split('/').map(x => x.replace(':', '')).filter(x => x ? true : false);
        const params = {};
        if (expression) {
            urls.forEach((val, i) => {
                const index = expression.urlParamsIndexes.indexOf(i);
                if (index !== -1) {
                    params[expression.urlParams[index]] = val;
                }
            });
        }

        return params;
    }



    /**
     * navigate to with params
     *
     */
    public navigateTo(hash: string, params: any = {}) {
        location.hash = this.setParams(hash, params);
    }



    /**
     * set params (internal)
     *
     */
    public setParams(hash: string, params: any = {}) {

        const findParamRegex = new RegExp(':([a-zA-Z0-9_]*)', 'g');

        let urlParams = hash.match(findParamRegex);
        if (urlParams) {
            urlParams = urlParams.map((x) => {
                return x = x.replace(':', '');
            });
        } else {
            urlParams = [];
        }
        const urlParamsIndexes: any[number] = [];
        const urls = hash.split('/').map(x => x.replace(':', '')).filter(x => x ? true : false);
        let newUrl = '';
        urls.forEach((val, i) => {

            if (params[val] !== undefined) {
                newUrl = newUrl + params[val];
                urlParamsIndexes.push(i);
            } else {
                newUrl = newUrl + `${val}`;
            }

            if ((urls.length - 1) !== i) {
                newUrl = newUrl + `/`;
            }

        });

        return newUrl;
    }



    /**
     * called when it starts
     *
     */
    private init() {
        window.addEventListener('hashchange', this.hashChangeBinded);
    }

    /**
     * called when it starts
     *
     */
    private parseAddedRouter(config: IRouteConfigurationInternal) {

        // do it exist ?
        if (this.config[config.name]) {
            console.error('router already exist, override not allowed', config.name);
        }

        // add to our config
        this.config[config.name] = config;
        this.config[config.name].childrenRouters = [];
        this.config[config.name].childrenMainRoutes = [];
        this.config[config.name].childrenMainRoutesRegex = [];

        this.config[config.name].routes.forEach((route, i: number) => {


            // set configuration
            route.belongsTo = config.name;
            route.index = i;
            route.parent = config.parent;

            if (route.parent) {
                if (!this.config[config.parent] && this.config[config.parent].routes && this.config[config.parent].routes.length > 0) {
                    console.error('parent route does not exist', config.parent, config.parentRoute);
                }
            }

            const cleanUrl = this.getRouteUrl(config, route.route);

            route.expression = this.getExpression(cleanUrl);
            route.href = '#' + cleanUrl.replace('*', '');
            route.cleanUrl = cleanUrl.replace('*', '');
            if (route.isNav === false || route.isNav === true) {
                console.warn('use "nav", "isNav" will be removed in the future');
            }
            route.isNav = route.notNav ? false : true; // TODO: remove on 1.0.0

            if (route.nav === false || route.nav === true) {
                route.isNav = route.nav;
            }

            this.routes.push(cleanUrl);
            this.routesData.push(route);
        });

        // any parent ? if so we need to add this as child ref
        if (config.parent) {

            // we also want to add ref to child in parent
            if (this.config[config.parent]) {
                this.config[config.parent].childrenRouters.push(config.name);
                this.config[config.parent].childrenMainRoutes.push(config.parentRoute);
                this.config[config.parent].childrenMainRoutesRegex.push(this.getExpression(config.parentRoute).fullUrlRegexWeakEndString);
            } else {
                console.error('parent router missing', config.parent);
            }
        }
    }

    /**
    * gets expression
    *
    */
    private getExpression(hash: string): IRouterExpression {

        // will find any ":whatever" in url
        const findParamRegex = new RegExp(':([a-zA-Z0-9_-]*)', 'g');

        let urlParams = hash.match(findParamRegex);
        if (urlParams) {
            urlParams = urlParams.map((x) => {
                return x = x.replace(':', '');
            });
        } else {
            urlParams = [];
        }
        const urlParamsIndexes: any[number] = [];
        const urls = hash.split('/').map(x => x.replace(':', '')).filter(x => x ? true : false);
        let fullUrlRegexString = '';
        let isWildCard = false;
        urls.forEach((val, i) => {

            if ((urls.length - 1) === i) {
                if (val && val.length > 0 && val.charAt(val.length - 1) === '*') {
                    isWildCard = true;
                    val = val.replace('*', '');
                    urls[i] = val;
                }
            }

            if (urlParams.indexOf(val) !== -1) {
                fullUrlRegexString = fullUrlRegexString + '([a-zA-Z0-9_-]*)';
                urlParamsIndexes.push(i);
            } else {
                fullUrlRegexString = fullUrlRegexString + `(${val})`;
            }

            if ((urls.length - 1) !== i) {
                fullUrlRegexString = fullUrlRegexString + `/`;
            }



        });


        this.allRoutes.push(isWildCard ? fullUrlRegexString + `($|/)` : fullUrlRegexString + `($|/$)`);

        return {
            fullUrlRegexString: isWildCard ? fullUrlRegexString + `($|/)` : fullUrlRegexString + `($|/$)`,
            fullUrlRegexWeakEndString: fullUrlRegexString,
            urls: urls,
            urlParamsIndexes: urlParamsIndexes,
            urlParams: urlParams
        };
    }



    /**
     * get route path
     *
     */
    private getRouteUrl(config: any, route: string) {
        let url = '';

        if (config.parent) {
            const tempUrl = this.getRouteUrl(this.config[config.parent], config.parentRoute);
            url = tempUrl + '/' + route;
        } else {
            url = route;
        }

        return url;
    }



    /**
    * find correct router
    *
    */
    private parseRouters(index: number) {
        this.isNavigating = true;
        this.urlHash = window.location.hash.replace('#', '');
        const route = this.getRoutesFor(this.loadedRouterElements[index].name, true)[this.loadedRouterElements[index].routeIndex];

        if (this.urlHash.search(route.expression.fullUrlRegexWeakEndString) === 0) {
            const routeConfig = this.config[this.loadedRouterElements[index].name];
            if (routeConfig && routeConfig.childrenMainRoutes.length > 0) {
                let found = false;
                routeConfig.childrenMainRoutesRegex.forEach((regx) => {
                    if (route.route.search(regx) === 0) {
                        found = true;
                    }
                });

                // if new params, we need to load parent again.
                const newParams = this.getParams(route.expression, this.urlHash);
                const oldParams = this.loadedRouterElements[index].currentParams;
                let paramsCheckOk = true;
                for (const k in newParams) {
                    if (newParams[k] !== oldParams[k]) {
                        paramsCheckOk = false;
                    }
                }
                // console.warn(u, this.loadedRouterElements[index].currentParams, fullCheck)

                if (found && paramsCheckOk) {
                    this.parseRouters(index + 1);
                } else {
                    this.routerViewInit(this.loadedRouterElements[index].name);
                }

            } else {
                this.routerViewInit(this.loadedRouterElements[index].name);
            }
        } else {
            this.routerViewInit(this.loadedRouterElements[index].name);
        }

    }



    /**
    * called when routerview is called or hashchange
    * loops routers and paths to find out what view to call for update
    *
    */
    private routerViewUpdate(caller: string) {
        this.isNavigating = true;
        // setTimeout(() => {

        this.urlHash = window.location.hash.replace('#', '');
        if (window.location.hash !== '#' && window.location.hash !== '') {
            let routeFound = false;
            let newRouter;
            let expression: any;
            let isAuthUrl: false;

            let useWeak = false;
            this.allRoutes.forEach((x: any) => {
                if (this.urlHash.search(x) === 0) {
                    useWeak = true;
                }
            });

            this.getRoutesFor(caller, true).forEach((route: any) => {
                if (this.urlHash.search(useWeak ? route.expression.fullUrlRegexWeakEndString : route.expression.fullUrlRegexString) === 0) {
                    this.curRoute = route.index;
                    newRouter = route.belongsTo;
                    expression = route.expression;
                    isAuthUrl = route.auth;
                    routeFound = true;
                }
            });

            if (!routeFound) {
                this.curRoute = 0;
                this.lastRouter = caller;
            } else {
                this.lastRouter = newRouter;
            }

            let unknownUrl: any;
            if (!routeFound && this.unknownCaller) {
                unknownUrl = this.unknownCaller.call(this.urlHash);
            }

            if (unknownUrl !== undefined) {
                window.location.hash = unknownUrl;
            } else {

                //
                let isAutenticated = true;
                if (this.authCaller && isAuthUrl) {
                    isAutenticated = this.authCaller.call(this.urlHash);
                }

                if (isAutenticated) {
                    this.eventAggregator.publish('mf-router' + this.lastRouter, this.getParams(expression, this.urlHash));
                }
            }



        }

        if (window.location.hash === '') {
            this.curRoute = 0;
            this.lastRouter = caller;
            this.eventAggregator.publish('mf-router' + this.lastRouter);
        }


    }


}
