import { ArrayObserverHandler } from '../binding/arrayObserverHandler';
import { PropertyObserverHandler } from '../binding/propertyObserverHandler';

export interface IListener {
    /**
     * Name, mostly useful for debugging
     */
    name?: string | null | undefined;

    /**
     * current value
     */
    value?: any;

    /**
     * function that will be called when changes happend
     */
    call: Function;

    /**
     * private, do not set, will be set by mframejs
     */
    caller?: ArrayObserverHandler | PropertyObserverHandler;
}
