import { ViewParser } from '../view/exported';
import { IElement, INode } from '../interface/exported';
import { DebounceBehavior, ThrottleBehavior, SignalBehavior, TriggerBehavior } from '../behavior/exported';
// built in attributes/elements
import { MfRouter } from '../element/exported';
import {
    IfAttribute,
    ValueAttribute,
    DelgateEventsAttribute,
    TriggerEventsAttribute,
    RepeatAttribute,
    MiscAttributes,
    CssAttribute,
    ModelAttribute
} from '../attribute/exported';



/**
 * Main framework class, this is returned in the configure function
 *
 */
export class MF {

    private node: INode;
    private app: IElement;
    public count = 0;


    constructor() {

        // polyfill IE11
        if (!(window as any).Reflect) {
            (window as any).Reflect = Object;
        }

        // register built in attributes/elements
        this.register(
            MfRouter,
            ValueAttribute,
            IfAttribute,
            DelgateEventsAttribute,
            RepeatAttribute,
            MiscAttributes,
            CssAttribute,
            ModelAttribute,
            TriggerEventsAttribute,
            SignalBehavior,
            ThrottleBehavior,
            DebounceBehavior,
            TriggerBehavior);
    }



    /**
     * sets node and app
     *
     */
    public setRootApp(app: IElement) {

        this.app = app;

        // return this instance
        return this;
    }



    /**
     * starts application
     *
     */
    public async start(node: INode) {

        this.node = node;

        // set framework to main node
        ViewParser.parseAndCreateElement(this.app, this.node, (this as any));

    }



    /**
     * register new custom attributes, custom elements
     *
     */
    public register(..._class: any[]) {

        // does not need to do anything really
        // by using them the decorator will kick in and they will be registered

    }

}
