import { Router } from '../utils/exported';
import { EventAggregator } from '../utils/exported';
import { customElement, bindable } from '../decorator/exported';
import { inject } from '../decorator/exported';
import { IElement } from '../interface/exported';
import { ContainerClasses } from '../container/exported';
import { ViewParser } from '../view/exported';
import { ViewController } from '../view/exported';



@customElement('mf-router')
@inject(EventAggregator, Router)
export class MfRouter implements IElement {

    public currentParams: any;
    public routeIndex: number;
    public route: string;

    // added by template engine
    public $element: HTMLElement;
    public $parent: IElement;
    public $view: ViewController;

    // current page instance
    private instance: IElement;

    // state
    private started: boolean;
    private loaded: boolean;
    private instanceOld: any;
    private view: any;

    // name
    @bindable({ attribute: 'router-name' }) public name = '';
    @bindable({ attribute: 'post-events' }) public postEvents: any;
    private updatebinded: any;



    constructor(private eventAggregator: EventAggregator, public router: Router) {

    }



    /**
     * created
     *
     */
    public created() {
        if (this.postEvents && this.postEvents.toUpperCase && this.postEvents.toUpperCase() === 'TRUE') {
            this.postEvents = true;
        } else {
            this.postEvents = false;
        }

        this.router.registerRouterElement(this);
    }



    /**
     * attached
     *
     */
    public attached() {

        // ref so we can unsubscribe
        this.updatebinded = this.update.bind(this);

        // check name
        if (this.name) {
            this.eventAggregator.subscribe('mf-router' + this.name, this.updatebinded);
        } else {
            console.error('router-name element attribute missing!');
        }

        // so we know...
        this.started = true;
        if (!this.loaded) {
            this.router.routerViewInit(this.name);
        }

    }



    /**
     * detached
     *
     */
    public detached() {
        if (this.instanceOld) {
            this.RemoveOldInstance();
        }

        this.router.unregisterRouterElement(this);
        // if this happens, then lets send out one incase
        this.eventAggregator.unsubscribe('mf-router' + this.name, this.updatebinded);
    }



    /**
     * loadTemplate
     *
     */
    public async loadTemplate() {

        // if router is not started then we wait until it calls us
        if (!this.started) {

            // you could have displayed something here...
            return '<template></template>';
        } else {

            // if route fails, then show error (good chance they pass in something we cant use)
            try {

                // get current route module
                this.route = this.router.getCurrentRoute(this.name);
                this.routeIndex = this.router.getCurrentRouteIndex(this.name);

                const template = await this.router.loadCurrentModule(this.name); // <-  this need to be async..
                if (template === undefined) {
                    console.warn('route not found' + this.route);
                }

                // do we have correct class?
                const className = this.router.getCurrentClass(this.name);
                if (!template || template && !template[className]) {
                    console.warn('route class not found:', className);
                }
                const instance = ContainerClasses.get(template[this.router.getCurrentClass(this.name)]);
                this.instance = instance;

                // return template
                if (instance.loadTemplate) {
                    return await instance.loadTemplate();
                } else {
                    console.error('missing loadtemplate', this.route);
                }

            } catch (e) {
                return `<template>${e}</template>`;
            }

        }
    }



    /**
     * update
     *
     */
    public update(params: any) {

        this.currentParams = params;

        if (this.instanceOld) {
            this.RemoveOldInstance();
        }

        // vars
        this.instanceOld = this.instance;
        let response = true;

        // if old, then check if we can deactivate
        if (this.instanceOld) {
            if (this.instanceOld.canDeactivate) {
                // switch to async ?
                response = this.instanceOld.canDeactivate();
                if (response) {
                    response = this.anyDeactivate(this.instanceOld.$element);
                }
            }
        }

        if (response) {

            // create a view put route in
            this.view = document.createElement('view');
            const view = this.view;
            view.style.display = 'none';


            // load templates
            this.loadTemplate().then((templateString) => {

                this.loaded = true;
                // module will be element in routes <view> tag
                const controller = ViewParser.parseAndCreateElement(this.instance, view, this, templateString);

                // add view to parent
                this.$element.appendChild(view);

                // call attached on controller so it know its connected to parent
                controller.attached();

                let returnPromise;
                if (this.instance.activate) {
                    returnPromise = this.instance.activate(params, this.router.getRoutesFor(this.name, true), this.routeIndex);
                }

                // if we have old instance then we need to remove it;
                Promise.resolve(returnPromise).then(() => {
                    if (this.view === view) {
                        view.style.display = 'block';
                        this.RemoveOldInstance();
                    }
                    if (this.instance.activated) {
                        this.instance.activated(params);
                    }

                });

                // if post event attibute is set then we post event
                // useful when having subrouter and parent page needs to display a change etc
                if (this.postEvents) {
                    this.eventAggregator.publish((`mf-router-${this.name}`), {
                        type: 'routeActivate',
                        route: this.route,
                        name: this.name,
                        routeIndex: this.routeIndex
                    });
                }

                // publich back to router so we know
                this.router.succeessRoute({
                    route: this.route,
                    success: true
                });

            });
        } else {
            this.instanceOld = null;
            this.router.succeessRoute({
                route: this.route,
                success: false
            });
        }


    }


    /**
     * remove old instance if any
     *
     */
    private RemoveOldInstance() {
        this.router.isNavigating = false;
        if (this.instanceOld && this.$element) {
            if (this.instanceOld.detached) {
                this.instanceOld.detached();
            }

            if (this.instanceOld.$element) {
                const node = this.$element.removeChild(this.instanceOld.$element);
                ViewParser.clearAllViews(node);
                this.instanceOld.$element = null;
                this.instanceOld.$parent = null;
                this.instanceOld.$attributes = null;
                this.instanceOld.$element = null;
            }
            this.instanceOld = null;
        }
    }



    /**
     * helper class, check if anycanDeactive in child routes/elements
     * if a custom element also have this then it will break (stop router)
     *
     */
    private anyDeactivate($el: any): boolean {
        let returnValue = true;
        const nodeFiler = function (node: any): number {
            return node.$view ? NodeFilter.FILTER_ACCEPT : NodeFilter.FILTER_REJECT;
        };

        const nodeIterator = document.createNodeIterator(
            $el,
            NodeFilter.SHOW_ALL,
            (nodeFiler as any),
            false
        );
        let currentNode;
        while (currentNode = nodeIterator.nextNode()) {
            if (currentNode && (<any>currentNode).$view) {
                const y = (currentNode as any).$view.items;
                for (const el in y) {
                    if (y[el]._class && y[el]._class.canDeactivate) {
                        returnValue = y[el]._class.canDeactivate();
                        if (!returnValue) {
                            return returnValue;
                        }
                    }
                }
            }
        }

        return returnValue;
    }


}
