

/**
 * queue used internally so some parts get time to breath
 *
 */
export class Queue {
    public count = 0;
    public tasks: any[] = [];
    public topQueue: any[] = [];
    public started = false;
    private index = -1;



    /**
     * returns true/false depending if it s finished
     *
     */
    public finsished(): boolean {
        return this.count === 0;
    }



    /**
     * add task
     *
     *
     */
    public add(task: any): void {
        this.count++;
        this.tasks.push(task);
        if (!this.started) {
            this.started = true;
            setTimeout(() => {
                this.flush();
            }, 0);
        }
    }



    /**
     * flushes the queue
     *
     */
    private flush(): void {

        this.tasks.reverse();
        if (this.tasks.length < 600) {
            this.topQueue = new Array(this.tasks.length);
            for (let i = 0, l = this.tasks.length; i < l; i++) {
                this.topQueue[i] = this.tasks.pop();
            }
        } else {
            if (this.topQueue.length !== 600) {
                this.topQueue = new Array(600);
            }
            for (let i = 0, l = 600; i < l; i++) {
                this.topQueue[i] = this.tasks.pop();
            }
        }
        this.tasks.reverse();
        this.index = 0;

        if (this.topQueue.length === 0) {
            this.started = false;
        }

        this.runTasks();
    }



    /**
     * reflushes the queue (usually if there is more)
     *
     */
    private reFlush(): void {
        setTimeout(() => {
            this.flush();
        }, 0);
    }



    /**
     * run the tasks
     *
     */
    private runTasks(): void {
        const curTask = this.topQueue[this.index];
        this.index++;

        let done: Function;
        done = () => {
            const nextTask = this.topQueue[this.index];
            this.index++;
            if (nextTask) {
                nextTask.call(done);
                this.count--;
            } else {
                if (this.index > this.topQueue.length - 1) {
                    this.reFlush();
                } else {
                    this.started = false;
                }
            }
        };
        if (curTask) {
            curTask.call(done);
            this.count--;
        }


    }

}

